<script lang="ts">
  import { Modal, Form, Slider, Button } from 'spaper'
  import { handleTxWithUx } from '../helpers/tx-helper'
  import { withdraw } from '../helpers/eth-helper'

  export let pid: number
  export let balance: number

  let value = 0

  async function withdrawHandler() {
    console.log(pid, value)
    await handleTxWithUx(
      'Withdraw LP',
      withdraw, [pid, value]
    )
  }
</script>

<Modal active={pid !== null} title="Withdraw LP">
  <Form>
    <Slider {value} max={balance} step={1/1e18} block />
  </Form>
  <svelte:fragment slot="footer">
    <Button type="warning"
            on:click={() => value = balance}>
      Max
    </Button>
    <Button on:click={withdrawHandler}>
      Withdraw
    </Button>
  </svelte:fragment>
</Modal>
